@import url("http://fonts.googleapis.com/earlyaccess/notosanskr.css");
@import url("https://cdn.jsdelivr.net/gh/moonspam/NanumSquare@1.0/nanumsquare.css");
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
  box-sizing: border-box;
}

html,
body,
div,
span,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
address,
cite,
code,
del,
dfn,
em,
font,
img,
ins,
q,
s,
samp,
strike,
strong,
sub,
sup,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend {
  margin: 0;
  padding: 0;
  line-height: 1.3em;
  border: 0 none;
  box-sizing: border-box;
}

body {
  margin: 0 auto;
  font-size: 100%;
  line-height: 1.3em;
  font-family: "Noto Sans", "Apple SD Gothic", sans-serif;
  font-weight: 400;
  word-break: keep-all;
  text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

sub,
sup {
  font-family: inherit;
}

.skip-nav {
  position: absolute;
  z-index: 9999;
  width: 100%;
}

.skip-nav a {
  display: block;
  overflow: hidden;
  width: inherit;
  height: 0;
  margin: 0 auto;
  margin-top: -1px;
  white-space: nowrap;
  text-align: center;
  text-indent: -9999px;
}

.skip-nav a:focus,
.skip-nav a:active {
  height: 34px;
  padding: 1px;
  border-bottom: 2px solid #ebe8e7;
  background: #666;
  color: #fff;
  font-weight: bold;
  font-size: 14px;
  text-indent: 0;
  line-height: 33px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: inherit;
  line-height: 1.1;
  font-weight: normal;
}

a:link {
  text-decoration: none;
}

ol,
ul,
li {
  list-style: none;
}

img {
  border: 0;
  vertical-align: top;
}

iframe {
  border: 0;
}

.invisible {
  position: absolute;
  left: -99999px;
  font-size: 0;
  line-height: 0;
  text-indent: -99999px;
  visibility: hidden;
}

.hidden {
  display: none;
}

.title-type-1 {
  font-size: 1.75rem;
  line-height: 1.35;
}

.text-type-1 {
  font-size: 1.25rem;
  line-height: 1.73;
}

/* button */
button[type='button'],
a.button,
input[type='submit'] {
  cursor: pointer;
  border-width: 0;
  border-style: solid;
  border-color: transparent;
  font-family: inherit;
  background: transparent;
  appearance: none;
  display: inline-block;
  line-height: 1.1;
  box-sizing: border-box;
  text-align: center;
  font-family: inherit;
  padding: 0 65px;
  height: 52px;
  line-height: 50px;
  color: #262626;
  font-size: 1.125rem;
  text-align: center;
  transition: all ease-in-out 300ms;
}

button[type='button']:first-letter,
a.button:first-letter,
input[type='submit']:first-letter {
  text-transform: uppercase;
}

button[type='button'].color-1,
a.button.color-1,
input[type='submit'].color-1 {
  border: 1px solid #393938;
}

button[type='button'].color-1:hover, button[type='button'].color-1:focus, button[type='button'].color-1:active,
a.button.color-1:hover,
a.button.color-1:focus,
a.button.color-1:active,
input[type='submit'].color-1:hover,
input[type='submit'].color-1:focus,
input[type='submit'].color-1:active {
  color: #292929;
  background-color: rgba(255, 255, 255, 0.1);
}

button[type='button'].size-l,
a.button.size-l,
input[type='submit'].size-l {
  height: 88px;
  line-height: 86px;
  padding: 0 230px;
  font-size: 1.375rem;
}

button[type='button'].block,
a.button.block,
input[type='submit'].block {
  display: block;
  width: 100%;
}

@media only screen and (min-width: 768px) and (max-width: 1199px) {
  button[type='button'],
  a.button,
  input[type='submit'] {
    height: 44px;
    line-height: 42px;
    padding: 0 30px;
    font-size: 0.875rem;
  }
  button[type='button'].size-l,
  a.button.size-l,
  input[type='submit'].size-l {
    display: block;
    height: 50px;
    line-height: 48px;
    padding: 0 100px;
    font-size: 0.75rem;
  }
}

@media only screen and (max-width: 767px) {
  button[type='button'],
  a.button,
  input[type='submit'] {
    height: 34px;
    line-height: 32px;
    padding: 0 35px;
    font-size: 0.6875rem;
  }
  button[type='button'].size-l,
  a.button.size-l,
  input[type='submit'].size-l {
    display: block;
    height: 40px;
    line-height: 38px;
    padding: 0 30px;
    font-size: 0.6875rem;
  }
}

input[type='email'],
input[type='text'] {
  background: transparent;
  border-style: solid;
  border-width: 0;
  box-sizing: border-box;
  border-color: transparent;
  width: 100%;
  height: 31px;
  line-height: 30px;
  padding: 0;
  color: #262626;
  font-size: 1.5625rem;
  border-radius: 0;
  background-color: none;
  outline: none;
  transition: all .2s ease-in-out;
}

textarea {
  height: 100px;
  padding-top: 16px;
  line-height: 1.1;
}

::-webkit-input-placeholder {
  /* Chrome/Opera/Safari */
  color: #5d5d5d;
}

::-moz-placeholder {
  /* Firefox 19+ */
  color: #5d5d5d;
}

:-ms-input-placeholder {
  /* IE 10+ */
  color: #5d5d5d;
}

:-moz-placeholder {
  /* Firefox 18- */
  color: #5d5d5d;
}

body {
  color: #262626;
  background-color: #f6f4f1;
  overscroll-behavior: none;
}

body.scroll-hidden {
  overflow: hidden;
}

#contents {
  position: relative;
}

.dim {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.1);
  z-index: 99;
}

#footer {
  color: #515151;
  background-color: #f1efeb;
}

#footer .container {
  display: flex;
}

#footer .container > div {
  width: 21%;
  padding: 50px 0 90px 42px;
  border-left: 1px solid #dddbd8;
}

#footer .container > div:first-child {
  width: 37%;
  padding-left: 50px;
  border-left: none;
}

#footer .container h3 {
  margin-bottom: 30px;
  font-size: 0.9375rem;
  font-weight: 400;
}

#footer .container .info > span {
  display: block;
  font-size: 0.8125rem;
  line-height: 1.77;
}

#footer .container .info > span.copy {
  margin-top: 50px;
  font-size: 0.875rem;
}

#footer .container .list-1 li {
  font-size: 0.8125rem;
  line-height: 1.77;
}

#footer .container .list-1 li a {
  color: #515151;
}

#footer .container .sns {
  margin: -10px 0 0 0;
}

#footer .container .sns li {
  margin: 10px 0 0 0;
  font-size: 0;
  line-height: 0;
  text-indent: -9999em;
}

#footer .container .sns li a {
  display: inline-block;
  width: 18px;
  height: 18px;
  font-size: 0;
  line-height: 0;
  text-indent: -9999em;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: contain;
}

#footer .container .sns li a.instagram {
  background-image: url("../image/common/instagram-icon.png");
}

#footer .container .sns li a.facebook {
  background-image: url("../image/common/facebook-icon.png");
}

@media only screen and (min-width: 768px) and (max-width: 1199px) {
  #footer .container {
    flex-wrap: wrap;
  }
  #footer .container > div {
    width: 50%;
    padding: 30px 0 30px 30px;
    border-left: none;
  }
  #footer .container > div:first-child {
    width: 50%;
    padding-left: 30px;
    order: 3;
  }
  #footer .container > div:nth-child(4) {
    order: 4;
  }
  #footer .container > div:nth-child(1), #footer .container > div:nth-child(4) {
    margin-top: 20px;
    border-top: 1px solid #dddbd8;
  }
  #footer .container h3 {
    margin-bottom: 25px;
  }
  #footer .container .info > span {
    font-size: 0.625rem;
    line-height: 1.7;
  }
  #footer .container .info > span.copy {
    margin-top: 35px;
    font-size: 0.625rem;
  }
  #footer .container .list-1 li {
    font-size: 0.625rem;
    line-height: 1.7;
  }
  #footer .container .sns {
    margin: 0;
  }
}

@media only screen and (max-width: 767px) {
  #footer .container {
    flex-wrap: wrap;
    flex-flow: columns;
  }
  #footer .container > div {
    width: 100%;
    padding: 0 0 0 20px;
    border-left: none;
    border-top: 1px solid #dddbd8;
  }
  #footer .container > div > div,
  #footer .container > div > ul {
    display: none;
    padding-bottom: 20px;
  }
  #footer .container > div:first-child {
    width: 100%;
    padding-left: 20px;
    order: 4;
  }
  #footer .container > div:nth-child(4) {
    order: 3;
  }
  #footer .container > div:nth-of-type(1n+2) h3:after {
    content: '';
    display: block;
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    width: 50px;
    background: url("../image/common/path-1.png") no-repeat center center;
    background-size: 11px 7px;
    transition: all .3s ease-in-out;
  }
  #footer .container > div.active h3:after {
    transform: rotate(-180deg);
  }
  #footer .container > div.active > div,
  #footer .container > div.active > ul {
    display: block;
  }
  #footer .container h3 {
    position: relative;
    height: 50px;
    line-height: 50px;
    margin-bottom: 0;
    font-size: 0.6875rem;
  }
  #footer .container .info {
    display: block;
  }
  #footer .container .info > span {
    font-size: 0.5rem;
    line-height: 1.75;
  }
  #footer .container .info > span.copy {
    margin-top: 35px;
    font-size: 0.5rem;
  }
  #footer .container .list-1 li {
    font-size: 0.5rem;
    line-height: 1.75;
  }
  #footer .container .sns {
    margin: 0;
  }
}
