@charset 'UTF-8';

// html5
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
  box-sizing: border-box;
}

html,
body,
div,
span,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
address,
cite,
code,
del,
dfn,
em,
font,
img,
ins,
q,
s,
samp,
strike,
strong,
sub,
sup,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend {
  margin: 0;
  padding: 0;
  line-height: 1.3em;
  border: 0 none;
  box-sizing: border-box;
}

body {
  margin: 0 auto;
  font-size: 100%;
  line-height: 1.3em;
  font-family: $font-base;
  font-weight: 400;
  word-break: keep-all;
  text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

sub,
sup {
  font-family: inherit;
}

// for screen reader
.skip-nav {
  position: absolute;
  z-index: 9999;
  width: 100%;

  a {
    display: block;
    overflow: hidden;
    width: inherit;
    height: 0;
    margin: 0 auto;
    margin-top: -1px;
    white-space: nowrap;
    text-align: center;
    text-indent: -9999px;
  }

  a:focus,
  a:active {
    height: 34px;
    padding: 1px;
    border-bottom: 2px solid #ebe8e7;
    background: #666;
    color: #fff;
    font-weight: bold;
    font-size: 14px;
    text-indent: 0;
    line-height: 33px;
  }
}


// heading group
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: inherit;
  line-height: 1.1;
  font-weight: normal;
}


// anchor
a {}

a:link {
  text-decoration: none;
}

ol,
ul,
li {
  list-style: none;
}

img {
  border:0;
  vertical-align: top;
}

iframe {
  border: 0;
}

.invisible {
  position: absolute;
  left: -99999px;
  font-size: 0;
  line-height: 0;
  text-indent: -99999px;
  visibility: hidden;
}

.hidden {
  display:none;
}